import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class SingleDateControl implements IControl {

    isRequired(): boolean {
        return !!this.require;
    }
    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.SingleDate;
    }

    /**
     * 
     */
    format: string;
    /**
     * 
     */
    returnFormat: string;

    /**
    * 
    */
    className: string;
    placeholder: string;
    require: boolean;
}