import { IValue } from "./ivalue";
import { ControlType } from "../controltype";
// CheckBoxValue'app-solution-checkbox'
// CheckBoxValue valuevaluetruetrue
export class CheckBoxValue implements IValue{
    clearValue(): void {
        this.value = [];
    }
    // 
    setOriginalValue(value: any[]): void {
        if(value.length){
            this.value = value.map(v => {
               if(typeof v == 'string'){
                 return true;
               }else {
                 return v;
               }
            });
         }else{
            this.value = value;
         }
    }
    // CheckBoxValuetrue
    getOriginalValue() {
        if(this.value.length){
          return this.value.map(v => {
            if(v === true){
              return "true";
            }else{
              return v;
            }
          });
        }
        return this.value;
    }
    getControlType(): ControlType {
        return ControlType.BoolCheck;
    }
    value: any[];
    // CheckBoxValue 
    constructor(value: any[] = []){
        this.value = value;
    }
    isEmpty(): boolean {
        return this.value.length == 0;
    }
}