import { QueryCondition } from '../querycondition';

/**
 * 
 */
export class FieldConfig {

  /**
   * ID
   */
  id?: string;

  /**
   * 
   */
  labelCode: string;

  /**
   * 
   */
  name?: string;

  /**
   * 
   */
  isChecked?: boolean;

  /**
   * 
   */
  isDisabled?: boolean;

  /**
   * 
   */
  placeHolder?: string;
  beginPlaceHolder?:string;
  endPlaceHolder?:string;

  control?: any;

  value?: any;

  constructor(qc: QueryCondition) {
    this.id = qc.id;
    this.labelCode = qc.fieldCode;
    this.name = qc.fieldName;
  }


}
