import { Directive, Input, OnInit, Optional, Self, Injector } from '@angular/core';
import { NgControl } from '@angular/forms';
import { QueryConditionComponent } from '../querycondition.component';

@Directive({
    selector: '[clear-fields]'
})
export class ClearFieldsDirective implements OnInit { 
    @Input('clear-fields') clearFields = '';
   
    constructor(@Optional() @Self() private ngCtrl: NgControl, private qcc: QueryConditionComponent, private inject: Injector) {} 

    ngOnInit() {
        if(this.clearFields && this.ngCtrl) {
            //console.log(this.ngCtrl, this.clearFields, this.qcc);
            //console.log(this.qcc.selectedFieldConditions);
            this.ngCtrl.valueChanges.subscribe( (v) => {
                this.qcc.fieldCodeOfClearFields = [];
                this.clearFields.split(',').forEach(n => {  
                    const conditionItem = this.qcc.selectedFieldConditions.find(c => c.fieldCode === n);
                    if (conditionItem) {
                        this.qcc.fieldCodeOfClearFields.push(n);
                        conditionItem.value.clearValue();
                    }
                });
            })
        }
    }
}