import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class SingleYearControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.SingleYear;
    }
    isRequired(): boolean {
        return !!this.require;
    }
    /**
     * 
     */
    format: string;
     /**
     * 
     */
    returnFormat:string;
    /**
     * 
    */
    className: string;
    placeholder: string;
    require: boolean;
    
    // 
    maxDate:string|Date|null;
    // 
    minDate:string|Date|null;
}