import { Component, Input, OnInit, OnChanges, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { ComboLookUpControl } from '../../entity/controltype/combolookup/combolookup-control';
import { ComboLookUpValue } from '../../entity/conditionvalue/combolookup-value';
import { SolutionService } from '../../service/solution.service';

@Component({
    selector: 'app-solution-combolookup',
    templateUrl: './combolookup.component.html',
})

export class ComboLookUpComponent implements OnInit,OnChanges{
   
    /**
     * QueryCondition
     */
    @Input() item:any;

    /**
     * 
     */
    @Input() changeFields: Array<string> = [];

    @Output('conditionChange') conditionChange = new EventEmitter();

    uri: string;
    idField: any;
    valueField: any;
    textField: string;
    textValue: any;
    mapFields: any;
    displayType: any;
    multiSelect: boolean;
    preEventCmd: any;
    postEventCmd: any;
    testId: string;
    placeholder: string;
    autoWidth: boolean;
    panelWidth: number;
    panelHeight: number;
    selectedValues = "";

    constructor(private solutionService: SolutionService){ }

    ngOnInit(): void {
        this.getComboLookUpBindingInfo();
        this.testId = this.solutionService.getGuid();
        this.placeholder = (this.item.placeHolder) ? (this.item.placeHolder) : (this.item.control.placeholder);
        this.autoWidth = false;
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.getComboLookUpBindingInfo();
        if(changes['changeFields'] && changes['changeFields'].currentValue && !changes['changeFields'].firstChange) {
            const isChange = changes['changeFields'].currentValue.some(field => field == this.item.fieldCode);
            if(isChange){
                this.textValue = (this.item.value as ComboLookUpValue).textValue;
            }
        }
    }

    valueChangeHandler(data: any){
        const helpValue = (this.item.value as ComboLookUpValue);
        helpValue.value = data.selections;
        helpValue.valueField = this.valueField;
        helpValue.textValue = helpValue.getTextValue(this.textField);
        this.textValue = helpValue.textValue;
        this.conditionChange.emit();
    }

    getComboLookUpBindingInfo(){
        const comboLookUpControl = (this.item.control as ComboLookUpControl);
        this.uri = comboLookUpControl.uri;
        this.idField = comboLookUpControl.idField;
        this.valueField = comboLookUpControl.valueField;
        this.textField = comboLookUpControl.textField;
        this.mapFields = comboLookUpControl.mapFields;
        this.displayType = comboLookUpControl.displayType;
        this.preEventCmd = comboLookUpControl.preEventCmd;
        this.postEventCmd = comboLookUpControl.postEventCmd;
        this.multiSelect = comboLookUpControl.singleSelect === true ? false : true;
        this.panelWidth = comboLookUpControl.panelWidth ? comboLookUpControl.panelWidth : 300;
        this.panelHeight = comboLookUpControl.panelHeight ? comboLookUpControl.panelHeight : 300;
        const comboLookUpValue = (this.item.value as ComboLookUpValue);
        if(comboLookUpValue.textValue == 'help-text-value-null'){//
            this.textValue = comboLookUpValue.getTextValue(this.textField);
        }else{
            this.textValue = comboLookUpValue.textValue;
        }

        if (comboLookUpValue && comboLookUpValue.value) {
            this.selectedValues = (comboLookUpValue.value || []).map(n => n[this.idField]).join(',');
        }
    }

    clearData(data: any){
        (this.item.value as ComboLookUpValue).clearValue();
        this.conditionChange.emit();
    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }
}