import { Component, Input, OnInit, OnChanges, Output, EventEmitter, Injector, ViewChild, SimpleChanges } from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { ControlType } from '../../entity/controltype';
import { SmartHelpContol } from '../../entity/controltype/smarthelp/smarthelpcontrol';
import { IControl } from '../../entity/controltype/icontrol';
import { SmartHelpValue } from '../../entity/conditionvalue/smarthelpvalue';
import { SolutionService } from '../../service/solution.service';
import { LookupGridComponent } from '@farris/ui-lookup';
import { of } from 'rxjs';

@Component({
    selector: 'app-solution-smarthelp',
    templateUrl: './smarthelp.component.html',
})

export class SolutionSmartHelpComponent implements OnInit, OnChanges {

    /**
     * QueryCondition
     */
    @Input() item: any;

    /**
     * 
     */
    @Input() changeFields: Array<string> = [];

    @Output('conditionChange') conditionChange = new EventEmitter();

    @Output('helpMapping') mappingEmitter = new EventEmitter();

    @ViewChild('lookup') lookup: LookupGridComponent;

    uri: string;
    textField: string;
    mapFields: any;
    idField: any;
    valueField: any;
    textValue: any;
    displayType: any;
    context: any;
    preEventCmd: any;
    postEventCmd: any;
    loadTreeDataType: string;
    enableFullTree: boolean;
    singleSelect: boolean;
    expandLevel: number;
    enableCascade: boolean;
    testId: string;
    placeholder: string;
    nosearch: boolean;
    displayFields: string;
    displayTextSeparator: string;
    editable: boolean;
    clearFields: string;
    // 
    dialogTitle: string;
    // 
    dialogWidth: number;
    // 
    dialogHeight: number;
    /**  */
    pageSize: number = 20;
    /**  */
    pageList: Array<number> = [10, 20, 30, 50, 100];

    constructor(private solutionService: SolutionService) { }

    ngOnInit(): void {
        this.getSmartHelpBindingInfo();
        this.testId = this.solutionService.getGuid();
        this.placeholder = (this.item.placeHolder) ? (this.item.placeHolder) : (this.item.control.placeholder);

        this.lookup.textChanged.subscribe((v) => {
            this.textChangedHandler(v);
        })
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.getSmartHelpBindingInfo();
        if (changes['changeFields'] && changes['changeFields'].currentValue && !changes['changeFields'].firstChange) {
            const isChange = changes['changeFields'].currentValue.some(field => field == this.item.fieldCode);
            if (isChange) {
                this.textValue = (this.item.value as SmartHelpValue).textValue;
            }
        }
    }

    //ITEM
    //control: SmartHelpContol {uri: "WuLiao.wOrgId", textField: "name", valueField: "code", mapFields: {}, idField: "id"}
    // fieldCode: "wOrgId"
    // fieldName: ""
    // id: "WuLiao.wOrgId"
    // value: SmartHelpValue {valueField: null, value: null}
    // valueType: 0
    getSmartHelpBindingInfo() {
        const smartHelpControl = (this.item.control as SmartHelpContol);
        this.uri = smartHelpControl.uri;
        this.textField = smartHelpControl.textField;
        this.idField = smartHelpControl.idField;
        this.valueField = smartHelpControl.valueField;
        this.displayType = smartHelpControl.displayType;
        this.mapFields = smartHelpControl.mapFields;
        this.context = smartHelpControl.context;
        this.preEventCmd = smartHelpControl.preEventCmd;
        this.postEventCmd = smartHelpControl.postEventCmd;
        this.enableFullTree = smartHelpControl.enableFullTree;
        this.loadTreeDataType = smartHelpControl.loadTreeDataType;
        this.singleSelect = smartHelpControl.singleSelect === false ? false : true;
        this.expandLevel = smartHelpControl.expandLevel;
        this.enableCascade = smartHelpControl.enableCascade;
        // 
        this.dialogTitle = smartHelpControl.dialogTitle?smartHelpControl.dialogTitle:'';
        // 
        this.dialogWidth = smartHelpControl.panelWidth;
        // 
        this.dialogHeight = smartHelpControl.panelHeight;
        const smartHelpValue = (this.item.value as SmartHelpValue);
        if (smartHelpValue.textValue == 'help-text-value-null') {//
            this.textValue = smartHelpValue.getTextValue(this.textField);
        } else {
            this.textValue = smartHelpValue.textValue;
        }
        this.nosearch = smartHelpControl.nosearch;//false
        this.displayFields = smartHelpControl.displayFields ? smartHelpControl.displayFields : '';
        this.displayTextSeparator = smartHelpControl.displayTextSeparator ? smartHelpControl.displayTextSeparator : '_';
        this.editable = smartHelpControl.editable;
        this.clearFields = smartHelpControl.clearFields;
        if (smartHelpControl.hasOwnProperty('pageSize') && smartHelpControl.pageSize) {
            this.pageSize = smartHelpControl.pageSize
        }
        if (smartHelpControl.hasOwnProperty('pageList') && smartHelpControl.pageList) {
            this.pageList = smartHelpControl.pageList.split(",").map(item => { return parseInt(item); })
        }
    }

    onBeforeOpen = () => {
        const t = this.item.value as SmartHelpValue;
        if (t && t.value) {
            const ids = t.value.map(n => n[this.idField || 'id']).join(',');
            this.lookup.displayValue = ids;
        }

        return of(true);
    }

    clearData(data: any) {
        (this.item.value as SmartHelpValue).clearValue();
        if (this.item.control.clear) {
            this.item.control.clear();
        }
        this.conditionChange.emit();
    }

    getConditionItemTitle(field: QueryCondition) {
        return this.solutionService.getConditionItemTitle(field);
    }

    /**
     * selectedItem
     * code: "code02"
        id: "id02"
        name: "02"
        version: "2019-10-21T00:00:00"
    */
    textChangedHandler(data: string) {//
        if (this.item && this.item.value) {
            const helpValue = (this.item.value as SmartHelpValue);
            helpValue.value = [data];
            helpValue.textValue = data;
            helpValue.isInputText = true;
            // this.textValue = helpValue.textValue;
            this.conditionChange.emit();
        }
    }

    selectedDataHandler(selectedItem: any) {//
        const helpValue = (this.item.value as SmartHelpValue);
        helpValue.value = this.singleSelect ? [selectedItem] : selectedItem;//
        helpValue.valueField = this.valueField;
        helpValue.textValue = this.textValue;
        helpValue.isInputText = false;
        this.conditionChange.emit();
    }
}