import { Component, Input, OnInit, Injector, OnChanges, SimpleChanges, EventEmitter, Output} from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { SolutionService } from '../../service/solution.service';
import { CheckBoxValue } from '../../entity/conditionvalue/checkbox-value';
import { CheckBoxControl } from '../../entity/controltype/checkbox/checkbox-control';


@Component({
    selector: 'app-solution-checkbox',
    templateUrl: './checkbox.component.html',
})

export class CheckBoxComponent implements OnInit, OnChanges {
   
    _item: QueryCondition;
    checkValue: any[];
    controlData: CheckBoxControl;
    @Input() 
    set item(nr: QueryCondition){
        this._item = nr;
    }

    get item(){
        return this._item;
    }
    testId:string;

    /**
     * 
     */
    @Input() changeFields: Array<string> = [];

    @Output('conditionChange') conditionChange = new EventEmitter();

    solutionService: SolutionService;

    constructor(private injector: Injector){
        this.solutionService = this.injector.get(SolutionService);
    }

    ngOnChanges(changes: SimpleChanges): void {
        if(changes['changeFields'] && changes['changeFields'].currentValue && !changes['changeFields'].firstChange) {
            const isChange = changes['changeFields'].currentValue.some(field => field == this.item.fieldCode);
            if(isChange){
                this.checkValue = (this.item.value as CheckBoxValue).getOriginalValue();
            }
        }
    }

    ngOnInit(): void {
        this.checkValue = this.item.value.getOriginalValue();
        this.controlData = this.item.control as CheckBoxControl;
        this.testId = this.solutionService.getGuid();
    }

    valueChanged(event: any[]){
        //[true], []
        if(event.length){
            this._item.value.setOriginalValue(event);
        }else{
            this._item.value.setOriginalValue([false]);
        }
        this.conditionChange.emit();
    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }
}