import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class DateTimeRangeControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.DateTimeRange;
    }
    isRequired(): boolean {
        return !!this.require;
    }
        /**
     * 
     */
    enableCustomFormat:boolean;

    /**
     * 
     */
    format: string;
         /**
     * 
     */
    returnFormat:string;

    /**
     * 
    */
    className: string;
    placeholder: string;
    require: boolean;
        // 
       // beginPlaceHolder:string;
        // 
      //  endPlaceHolder:string;
    
    //weekSelect: boolean;
    //showWeekNumbers: boolean;
}