import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class SingleNumberControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.SingleNumber;
    }
    isRequired(): boolean {
        return !!this.require;
    }
    /**
     * 
     */
    length: number;

    /**
     * 
     */
    precision: number;
    /**
    * 
    */
    className: string;

    placeholder: string;

    require: boolean;

    textAlign: string;

    bigNumber: boolean;

    min: number;
    max: number;
}