import { Directive, Input, OnInit, Injector, ElementRef, NgZone } from '@angular/core';

@Directive({
    selector: '[label-text-multi]'
})
export class LabelTextMultiDirective implements OnInit {
    private multiCls = 'farris-group-multi-label';
    private _enableMulti = false;
    @Input('label-text-multi')
    set enableMulti(value: boolean) {
        this._enableMulti = value;
        this.judgeMulti();
    }
    get enableMulti() {
        return this._enableMulti
    }
    // ngZone
    private ngZone = null;
    constructor(private el: ElementRef, private injector: Injector) {
        if (!this.ngZone) {
            this.ngZone = this.injector.get<NgZone>(NgZone, null);
        }
    }

    ngOnInit() {

    }
    judgeMulti() {
        if (this._enableMulti) {
            this.ngZone.runOutsideAngular(() => {

                setTimeout(() => {
                    let textEl = this.el.nativeElement.querySelector('.farris-label-text');
                    if (!textEl) {
                        return;
                    }
                    // 
                    if (textEl.clientWidth < textEl.scrollWidth) {
                        this.el.nativeElement.className = this.el.nativeElement.className + ' ' + this.multiCls;
                    } else {
                        this.el.nativeElement.className = this.el.nativeElement.className.replace(this.multiCls, '');
                    }
                }, 20);
            });

        }
    }
}