import { Component, Input, OnInit, OnChanges, EventEmitter, Output, SimpleChanges } from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { ControlType } from '../../entity/controltype';
import { DropDownListControl } from '../../entity/controltype/dropdownlist/dropdownlistcontrol';
import { EnumType } from '../../entity/controltype/dropdownlist/enumtype';
import { EnumIntegerValue } from '../../entity/controltype/dropdownlist/integerenumvalue';
import { EnumStringValue } from '../../entity/controltype/dropdownlist/stringenumvalue';
import { EnumBoolValue } from '../../entity/controltype/dropdownlist/boolenumvalue';
import { SolutionService } from '../../service/solution.service';
import { IValue } from '../../entity/conditionvalue/ivalue';
import { DropDownListValue } from '../../entity/conditionvalue/dropdownlistvalue';

@Component({
    selector: 'app-solution-dropdownlist',
    templateUrl: './dropdownlist.component.html',
})

export class DropDownListComponent implements OnInit, OnChanges {
    /**
     * QueryCondition
     */
    @Input() item: any;

    /**
     * 
     */
    @Input() changeFields: Array<string> = [];

    @Output('conditionChange') conditionChange = new EventEmitter();

    //
    dropDownListDataSource: Array<any> = new Array<any>();
    //
    selectedItemId: any = '';
    //id
    testId: string;
    //placeholderitem.control
    placeholder: string;

    constructor(private solutionService: SolutionService) {}

    ngOnInit(): void {
        this.getDataSource();
        const initialValue = this.item.value as DropDownListValue;
        initialValue.key && (this.selectedItemId = initialValue.key);
        this.testId = this.solutionService.getGuid();
        this.placeholder = (this.item.placeHolder) ? (this.item.placeHolder) : (this.item.control.placeholder);
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.getDataSource();
        if(changes['changeFields'] && changes['changeFields'].currentValue && !changes['changeFields'].firstChange) {
            const isChange = changes['changeFields'].currentValue.some(field => field == this.item.fieldCode);
            if(isChange){
                this.selectedItemId = (this.item.value as DropDownListValue).key;
            }
        }
    }

    getDataSource() {
        this.dropDownListDataSource = (this.item.control as DropDownListControl).enumValues;
    }

    selectChange(selected: any){
        this.item.value = new DropDownListValue({
            value : selected['selections'],
            key :  selected['value']
        });
        this.conditionChange.emit();
    }

    onClear(event: any){
        this.item.value = new DropDownListValue();
    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }
}