import { Component, Input, OnInit, OnChanges, EventEmitter, Output} from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { SolutionService } from '../../service/solution.service';
import { TextValue } from '../../entity/conditionvalue/textvalue';

@Component({
    selector: 'app-solution-input',
    templateUrl: './input.component.html',
})

export class InputComponent implements OnInit{
    /**
     * QueryCondition
     */
    _inputItem: QueryCondition;

    placeholder: string;

    @Input()
    set item(textItem: any) {
        this._inputItem = textItem;
    }
    get item(): any{
        return this.constructTextItem(this._inputItem);
    }
    testId: string;

    @Output('conditionChange') conditionChange = new EventEmitter();

    constructTextItem(textItem: QueryCondition): QueryCondition {
        const textValueObj = textItem.value as TextValue;
        textValueObj.value = textValueObj.value && textValueObj.value.trim();
        textItem.value = textValueObj;
        return textItem;
    }

    constructor(private solutionService:SolutionService){}

    ngOnInit(): void {
        this.testId = this.solutionService.getGuid();
        this.placeholder = (this.item.placeHolder) ? (this.item.placeHolder) : (this.item.control.placeholder);
    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }
    
    onBlur(){
        this.conditionChange.emit();
    }
}