import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";

/**
 * 
 */
export class ComboLookUpControl implements IControl {

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.ComboLookUp;
    }
    isRequired(): boolean {
        return !!this.require;
    }
    /**
     * 
     */
    uri: string;

    /**
     * 
     */
    idField: any;

    /**
     * 
     */
    valueField: string;

    /**
     * 
     */
    textField: string;

    /**
     * 
     */
    mapFields: any;

    /**
     * 
     */
    displayType: any;

    /**
     * 
     */
    preEventCmd: any;

    /**
     * 
     */
    postEventCmd: any;

    /**
     * 
     */
    singleSelect: boolean;

    /**
     * 
     */
    className: string;

    placeholder: string;

    panelWidth: number;

    panelHeight: number;

    require: boolean;
}