import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";
interface Checkbox {
    value: any;
    name: any;
}
/**
 * 
 */
export class CheckBoxControl implements IControl {
    
    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.BoolCheck;
    }
    isRequired(): boolean {
        return !!this.require;
    }
    /**
    * 
    */
    className: string;
    /* radio  */
    data: Checkbox[] = [];
    /* name */
    name: string;
    /*  */
    horizontal: boolean;
    /*  */
    disable: boolean;
    /*  */
    value: any;
    /*  */
    separator = ',';
    /* string */
    isStringValue = true;

    require: boolean;
}